#SL-container {
  max-width: 428px;
  margin: 40px 20px;
  display: flex;
}

#SL-container h1, #SL-container h2, #SL-container h3, #SL-container p {
  margin-block: 0;
}

#SL-container #SL-support {
  display: none;
}

#SL-container #SL-title {
  letter-spacing: -1.28px;
  margin-bottom: 40px;
  font-size: 32px;
  font-weight: 700;
  line-height: 38px;
}

#SL-container #SL-title span {
  color: var(--purple-accent-color);
}

#SL-container #SL-flex {
  flex-direction: column;
  align-items: center;
  display: flex;
}

#SL-container #SL-flex #SL-placeholder {
  flex-direction: column;
  gap: 14px;
  width: 256px;
  margin-top: 120px;
  display: flex;
}

#SL-container #SL-flex #SL-placeholder p {
  color: #0009;
  text-align: center;
  letter-spacing: -.28px;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}

#SL-container #SL-flex #SL-list {
  flex-direction: column;
  gap: 20px;
  padding: 0;
  list-style: none;
  display: flex;
}

#SL-container #SL-flex #SL-list li.SL-book {
  background: #fff;
  border: 2px solid #4f2ee866;
  border-radius: 16px;
  flex-direction: row;
  width: 335px;
  height: 170px;
  padding: 14px;
  display: flex;
  position: relative;
}

#SL-container #SL-flex #SL-list li.SL-book img.SL-book-cover {
  width: 100px;
  height: 142px;
  margin-right: 14px;
}

#SL-container #SL-flex #SL-list li.SL-book button.SL-remove {
  background-color: #4f2ee8;
  border: none;
  border-radius: 100000px;
  justify-content: center;
  align-items: center;
  width: 28px;
  height: 28px;
  display: flex;
  position: absolute;
  top: 14px;
  right: 14px;
}

#SL-container #SL-flex #SL-list li.SL-book button.SL-remove svg {
  fill: #4f2ee8;
  width: 16px;
  height: 16px;
}

#SL-container #SL-flex #SL-list li.SL-book .SL-d1 {
  flex-direction: column;
  width: 193px;
  display: flex;
}

#SL-container #SL-flex #SL-list li.SL-book .SL-d1 h3.SL-book-title {
  letter-spacing: -.64px;
  text-transform: uppercase;
  margin-bottom: 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

#SL-container #SL-flex #SL-list li.SL-book .SL-d1 p.SL-book-category {
  color: #b4afaf;
  letter-spacing: .36px;
  margin-bottom: 8px;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
}

#SL-container #SL-flex #SL-list li.SL-book .SL-d1 p.SL-book-description {
  text-overflow: ellipsis;
  white-space: pre-wrap;
  letter-spacing: -.56px;
  height: 70px;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  overflow: hidden;
}

#SL-container #SL-flex #SL-list li.SL-book .SL-d1 .SL-d2 {
  flex-direction: row;
  justify-content: space-between;
  margin-top: auto;
  display: flex;
}

#SL-container #SL-flex #SL-list li.SL-book .SL-d1 .SL-d2 p.SL-book-author {
  color: #b4afaf;
  letter-spacing: -.48px;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 14px;
}

#SL-container #SL-flex #SL-list li.SL-book .SL-d1 .SL-d2 .SL-d3 {
  gap: 8px;
}

#SL-container #SL-flex #SL-list li.SL-book .SL-d1 .SL-d2 .SL-d3 :first-child {
  width: 32px;
  height: 11px;
}

#SL-container #SL-flex #SL-list li.SL-book .SL-d1 .SL-d2 .SL-d3 :last-child {
  width: 16px;
  height: 16px;
}

@media screen and (min-width: 767px) {
  #SL-container {
    max-width: 768px;
    margin: 40px 32px;
  }

  #SL-container #SL-title {
    letter-spacing: -1.92px;
    margin-bottom: 40px;
    font-size: 48px;
    line-height: 52px;
  }

  #SL-container #SL-placeholder {
    gap: 20px;
    margin-top: 140px;
  }
}

@media screen and (min-width: 1200px) {
  #SL-container {
    max-width: 1158px;
    margin: 40px 24px;
  }

  #SL-container #SL-support {
    display: flex;
  }
}

